@import url(https://fonts.googleapis.com/css?family=Roboto:100,400,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald:200&display=swap);
body {
  margin: 0;
  padding: 0;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-image: url(/React-Weather/static/media/sky.00daa64d.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root {
  --family-font: 'Roboto', sans-serif;
  --light-weight: '100';
  --normal-weight: '400';
  --title-weight: '900';
  --white-color: #fff;
  --bg-color: rgba(0, 0, 0, 0.85);
  --input-bg-color: rgba(255, 255, 255, 0.2);
  --error-title-size: 2.5em;
}

.row {
  display: flex;
  flex-direction: row;
}

.sectionTitle {
  font-family: 'Roboto', sans-serif;
  font-family: var(--family-font);
  font-weight: '900';
  font-weight: var(--title-weight);
  color: #fff;
  color: var(--white-color);
}

.sectionText {
  font-family: 'Roboto', sans-serif;
  font-family: var(--family-font);
  font-weight: '400';
  font-weight: var(--normal-weight);
  color: #fff;
  color: var(--white-color);
}

.sectionTextLight {
  font-weight: 100;
  font-family: 'Roboto', sans-serif;
  font-family: var(--family-font);
  color: #fff;
  color: var(--white-color);
}

.container {
  background-color: rgba(0, 0, 0, 0.85);
  background-color: var(--bg-color);
  width: 85vw;
  height: 85vh;
  -webkit-animation: appear 400ms;
          animation: appear 400ms;
}

@-webkit-keyframes appear-slide {
  0% {
    -webkit-transform: translateX(-15%);
            transform: translateX(-15%);
    opacity:0;
  }
  
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity:1;
  }

}

@keyframes appear-slide {
  0% {
    -webkit-transform: translateX(-15%);
            transform: translateX(-15%);
    opacity:0;
  }
  
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    opacity:1;
  }

}

@-webkit-keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.CityInput {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 1.5em;
  padding: 23px;
  color: var(--white-color);
  background-color: var(--input-bg-color);
  border: 0;
}

.SearchBarContainer {
    text-align: right;
}

.submit-button {
  border: 1px solid var(--input-bg-color);
  padding:22px;
  -webkit-transition: 400ms ease;
  transition: 400ms ease;
}

.submit-button:hover {
  cursor: pointer;
  background-color: var(--bg-color);
}
.Temperature {
  display: flex;
  justify-content: center;
  padding: 100px;
}

.TemperatureTitle {
  font-family: "Oswald", sans-serif;
  font-weight: 900;
  color: #fff;
  font-size: 10em;
  margin: 0 20px;
  -webkit-animation: appear 3500ms;
          animation: appear 3500ms;
}

.TemperatureIcon {
  height: 5em;
  -webkit-animation: appear 1000ms;
          animation: appear 1000ms;
}

.TemperatureInfoContainer {
  width: 500px;
  -webkit-animation: appear-slide 4s;
          animation: appear-slide 4s;
  -webkit-animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
          animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}

.TemperatureInfo span {
  display: flex;
  align-items: flex-end;
  margin: 15px;
}

.row .sectionText,
.row .sectionTitle {
  margin: 5px 0 10px 25px;
  overflow: hidden;
}

.TemperatureInfoTitle {
  font-size: 2em;
  font-weight: 200;
  border-bottom: 1px solid #fff;
}

@media (max-width: 768px) {
  .CityInput {
    width: inherit;
    padding: 10px 0 10px 15px;
  }

  .TemperatureTitle {
    font-size: 7em;
  }

  .TemperatureInfoContainer {
    margin-top: 50px;
  }

  .Temperature {
    display: block;
  }

  .GeneralContainer {
    margin-top: 50px;
  }

  .GeneralContainer .row {
    justify-content: center;
  }
}

.CircleAnimation {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.CircleAnimation div {
  position: absolute;
  border: 4px solid #222;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.CircleAnimation div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}

.ErrorInfoContainer {
    display:flex;
    justify-content: center;
    align-items: center;
    width:500px;
    height:200px;
}

.ErrorCenter {
    display: flex;
    justify-content: center;
    align-content: center;
}

.errorTitle {
    font-size: var(--error-title-size);
    font-weight: var(--normal-weight);
}

.errorText {
    font-weight: var(--light-weight);
}
.GeneralContainer {
    display:flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin:25px;
    text-align: center;
}

.GeneralTitle {
    display:flex;
    flex-direction: column;
    text-align: center;
    margin:10px;
}

.GeneralTitle .sectionTitle {
    font-size: 2em;
    margin:5px;
    -webkit-animation: appear 2s;
            animation: appear 2s;
}

.GeneralTitle .sectionTextLight {
    font-size:2em;
    -webkit-animation: appear 2.5s;
            animation: appear 2.5s;
}

.GeneralTitle + .row .sectionTextLight {
    margin:10px;
}
.HomeContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top:-75px;
}

.HomeContainer h1,
.HomeContainer h2 {
    font-size: 3em;
    margin:20px;
}
